import { useContext, useEffect, useState } from "react";
import { Row, Col, Button, Alert, Form, Nav } from "react-bootstrap";
import { useParams } from "react-router-dom";
import { Context1 } from "../App.js";
import { useDispatch, useSelector } from "react-redux";
import { addCart } from "../store.js";



function Detail(props) {

  
  let dispatch = useDispatch();

  let {stock} = useContext(Context1);

  let [hide, setHide] = useState(true);
  let [ifNumber, setIfNumber] = useState(true);
  let [tab, setTab] = useState(0);
  
  let {id} = useParams();
  // console.log(id);
  
  let result = props.shoes.find((item) => item.id == id);
  // console.log(result);

  let [fade, setFade] = useState('');
  useEffect(()=>{
    setTimeout(()=>{
      setFade('end')
    }, 100)

    return ()=>{
      
      setFade('')
    }
  }, [])

  useEffect(()=>{
    let a = setTimeout(()=>{setHide(false);}, 2000);

    return () => {
      clearTimeout(a);
    }
  }, []);

  useEffect(()=>{
    var a = JSON.parse(localStorage.getItem('watched'));
    a.unshift(result.id);
    let b = new Set(a)
    let c = [...b]
    localStorage.setItem('watched', JSON.stringify(c));
  },[])

  return (
    <div className={'start '+ fade}>
    {
      hide == true
      ? <Alert key='warning' variant='warning'>2초 이내 구매시 할인!!!!</Alert>
      : null
    }
    
    <Row className="align-items-center">
      <Col md={6}>
        <img src={'https://codingapple1.github.io/shop/shoes'+ (parseInt(id) + 1) + '.jpg'} width="100%" />
      </Col>
      <Col md={6}>
        <Form>
          <Form.Group>
            <Form.Label>수량을 입력하세요</Form.Label>
            <Form.Control className='mb-3' type="text" onChange={ ((e)=>{
              if(isNaN(e.target.value) === true) {
                setIfNumber(false);
              } else {
                setIfNumber(true)
              }
            }) } />
            {
              ifNumber == true
              ? null
              : <Alert key='danger' variant='danger'>경고 : 숫자만 입력하세요</Alert>
            }
          </Form.Group>
        </Form>
        <h4 className="pt-5">{result.title}</h4>
        <p>{result.content}</p>
        <p>{result.price} Won</p>
        <Button className="btn btn-danger mb-5"
         onClick={()=>{
          dispatch(addCart({'id' : result.id, 'name' : result.title, 'price' : result.price, 'count' : 1}));
          // console.log(state);
        }}
        >주문하기</Button> 
        
      </Col>
    </Row>

    <Nav fill variant="tabs" defaultActiveKey="link0">
      <Nav.Item>
        <Nav.Link onClick={()=>{ setTab(0) }} eventKey="link0">Link0</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link onClick={()=>{ setTab(1) }} eventKey="link1">Link1</Nav.Link>
      </Nav.Item>
      <Nav.Item>
      <Nav.Link onClick={()=>{ setTab(2) }} eventKey="link2">Link2</Nav.Link>
      </Nav.Item>
    </Nav>
      <TabContent shoes={props.shoes} tab={tab}/>
      
    </div>
    
  )
}

function TabContent(props){
  let [fade, setFade] = useState('');
  let {stock} = useContext(Context1);
  
  useEffect(()=>{
    setTimeout(()=>{
      setFade('end')
    }, 100)

    return ()=>{
      
      setFade('')
    }
  }, [props.tab])

  if (props.tab == 0){
    return <div className={'start '+ fade}>내용0</div>
  }
  if (props.tab == 1) {
    return <div className={'start '+ fade}>내용1</div>
  }
  if (props.tab == 2) {
    return <div className={'start '+ fade}>내용2</div>
  }
}



export default Detail;